@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.about-section {
    margin-top: -30px;
    text-align: center;
    height: 500px;
}
.about-text-container {
    margin-left: 8%;
    width: 40%;


}

.intro {
    font-size: 68px;
    margin-top: 50px;
    height: 700px;
}
.intro-text {
    color: aliceblue;
}
.links {
    text-align: center;
    margin: 90px;
}
a {
    padding: 8px;
}
@media (max-width: 600px) {

    a {
        padding: 5px;
    }
    .about-text-container {
        display: inline;
        justify-content: center;
        text-align: left;
    }

.about-text-container p, h1{
    padding: 0 50px;
}
}
#footer {
    text-align: center;
    padding: 20px;
    color: grey;
  }
  
#myVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%; 
    min-height: 100%;
}
.logoName {
    font-family: 'Fredoka One', cursive;
}
